.navbar{
    color: white;
    font-size: 14px;
    width: 100%;
    position: fixed;
    top: 0;
    z-index: 999;
    background: linear-gradient(to top, transparent 0%,rgb(0, 0, 0, 0.3) 50%);

    &.scrolled{
        background-color: var(--main-color);
    }



    .container{
        padding: 0px 50px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 70px;



        .left{
            display: flex;
            align-items: center;
            img{
                height: 60px;;
                margin-right: 40px;
            }

            span{
                margin-right: 20px;
                cursor: pointer;
            }     
        }

        
        .right{
            display: flex;
            align-items: center;
            img{
                height: 30px;
                width: 30px;
                border-radius: 5px;
                object-fit: cover;
                cursor: pointer;
                 }

            }
        
        img{
            height: 25px;
        }

        .icons{
            margin: 0px 15px;
            cursor: pointer;

        }

        &:hover{
            color:#818181;
        }

        .profile{
            .options{
                display: none;
                background-color: var(--main-color);
                border-radius: 5px;
            }

            span{
                padding: 10px;
                cursor: pointer;
            }

            &:hover{
                .options{
                    display: flex;
                    flex-direction: column;
                    position: absolute;

                }
            }
        }
    }
}